<template>
  <div>
    <div id="diagram_chart"></div>
    <button @click="build">Redesenhar</button>
  </div>
</template>

<script>
import * as d3 from 'd3';

const height = 400;
const width = 300;

const trianglesLineWidth = 3;

const trianglesStartY = 50;
const trianglesSize = 22.5;
const trianglesTopPoint = trianglesStartY - trianglesSize;

export default {
  mounted() {
    this.build();
  },
  props: {
    a: {
      type: Number,
      required: true,
    },
    b: {
      type: Number,
      required: true,
    },
    c: {
      type: Number,
      required: true,
    },
    p1Cut: {
      type: Number,
      required: true,
    },
    p2Cut: {
      type: Number,
      required: true,
    },
    mMax: {
      type: Number,
      required: true,
    },
  },
  computed: {
    totalLength() {
      return this.a + this.b + this.c;
    },
    aX() {
      return this.distanceToX1 + this.scaledA;
    },
    aY() {
      return trianglesTopPoint + this.p1Cut;
    },
    cY() {
      return trianglesTopPoint + this.p2Cut;
    },
    mMaxCalc() {
      let max = this.aY;

      if (this.cY > max) {
        max = this.cY;
      }

      return max + this.mMax;
    },
    cX() {
      return this.distanceToX2 - this.scaledC;
    },
    scaledA() {
      return width * this.a / this.totalLength;
    },
    scaledB() {
      return width * this.b / this.totalLength;
    },
    scaledC() {
      return width * this.a / this.totalLength;
    },
    distanceToX1() {
      return trianglesSize + trianglesLineWidth;
    },
    distanceToX2() {
      return width - trianglesSize - trianglesLineWidth;
    },
  },
  methods: {
    leftTrianglePoints() {
      const firstPoint = `${0 + trianglesLineWidth},${trianglesStartY}`;
      const secondPoint = `${trianglesLineWidth + trianglesSize},${trianglesTopPoint}`;
      const thirdPoint = `${2 * trianglesSize + trianglesLineWidth},${trianglesStartY}`;

      return `${firstPoint}, ${secondPoint}, ${thirdPoint}`;
    },
    rightTrianglePoints() {
      const firstPoint = `${width - trianglesLineWidth},${trianglesStartY}`;
      const secondPoint = `${width - trianglesLineWidth - trianglesSize},${trianglesTopPoint}`;
      const thirdPoint = `${width - 2 * trianglesSize - trianglesLineWidth},${trianglesStartY}`;

      return `${firstPoint}, ${secondPoint}, ${thirdPoint}`;
    },
    curveLineFunction() {
      return d3.line()
        .curve(d3.curveBasis)
        .x(d => d.x)
        .y(d => d.y);
    },
    lineFunction() {
      return d3.line()
        .x(d => d.x)
        .y(d => d.y);
    },
    build() {
      document.querySelector('#diagram_chart').innerHTML = '';
      const svg = d3
        .select('#diagram_chart')
        .append('svg')
        .attr('height', height)
        .attr('width', width);

      svg.append('polygon')
        .style('fill', 'none')
        .style('stroke-width', trianglesLineWidth)
        .style('stroke', 'black')
        .attr('points', this.leftTrianglePoints());

      svg.append('polygon')
        .style('fill', 'none')
        .style('stroke-width', trianglesLineWidth)
        .style('stroke', 'black')
        .attr('points', this.rightTrianglePoints());

      svg.append('line')
        .style('stroke', 'black')
        .style('stroke-width', trianglesLineWidth)
        .attr('x1', trianglesSize + trianglesLineWidth)
        .attr('x2', width - trianglesSize - trianglesLineWidth)
        .attr('y1', trianglesTopPoint)
        .attr('y2', trianglesTopPoint);


      const firstLineData = [
        { x: trianglesSize + trianglesLineWidth, y: trianglesTopPoint },
        { x: this.aX, y: this.aY },
      ];

      const secondLineData = [
        { x: this.aX, y: this.aY },
        { x: (this.aX + this.cX) / 2, y: this.mMaxCalc },
        { x: this.cX, y: this.cY },
      ];

      const thirdLineData = [
        { x: this.cX, y: this.cY },
        { x: width - trianglesSize - trianglesLineWidth, y: trianglesTopPoint },
      ];

      svg.append('path')
        .attr('d', this.lineFunction()(firstLineData))
        .attr('stroke-linecap', 'round')
        .style('stroke', 'blue')
        .style('stroke-width', trianglesLineWidth)
        .style('fill', 'none');

      svg.append('path')
        .attr('d', this.curveLineFunction()(secondLineData))
        .attr('stroke-linecap', 'round')
        .style('stroke', 'blue')
        .style('stroke-width', trianglesLineWidth)
        .style('fill', 'none');

      svg.append('path')
        .attr('d', this.lineFunction()(thirdLineData))
        .attr('stroke-linecap', 'round')
        .style('stroke', 'blue')
        .style('stroke-width', trianglesLineWidth)
        .style('fill', 'none');

      svg.append('text')
        .attr('x', this.aX)
        .attr('y', this.aY + 20)
        .text(this.p1Cut.toFixed(2));

      svg.append('text')
        .attr('x', this.cX)
        .attr('y', this.cY + 20)
        .text(this.p2Cut.toFixed(2));
    },
  },
};
</script>

<style>
#diagram_chart {
  height: 400px;
  width: 300px;
  background: #f3f3f3;
}
</style>



