<template>
  <div class="hello">
    <div class="center">
      <img src="../assets/base.png" />
    </div>

    <form class="row">
      <div class="form-group col-6">
        <label for="">Distancia a</label>
        <input @input="setData" type="number" name="a" class="form-control"/>
      </div>

      <div class="form-group col-6">
        <label for="">Distancia b</label>
        <input @input="setData" type="number" name="b" class="form-control" />
      </div>

      <div class="form-group col-6">
        <label for="">Distancia c</label>
        <input @input="setData" type="number" name="c" class="form-control" />
      </div>

      <div class="form-group col-6">
        <label for="">Carga p¹</label>
        <input @input="setData" type="number" name="p1" class="form-control" />
      </div>

      <div class="form-group col-6">
        <label for="">Carga p²</label>
        <input @input="setData" type="number" name="p2" class="form-control" />
      </div>

      <div class="form-group col-6">
        <label for="">Carga distribuída</label>
        <input @input="setData" type="number" name="q" class="form-control" />
      </div>
    </form>

    <button class="center btn btn-primary" @click="calculate">Calcular!</button>

    <p>Somatório em Y: {{ sumY }}</p>
    <p>Somatório  em X: {{ sumX }}</p>
    <p>Somatorio Momento em A: {{ sumMA }} </p>

    <p>By: {{ sumMA }}</p>
    <p>Ay: {{ aY }}</p>
    <p>Ax: 0</p>

    <DiagramChart
      v-if="show"
      :a="a"
      :b="b"
      :c="c"
      :mMax="mMax"
      :p1Cut="p1Cut"
      :p2Cut="p2Cut" />
  </div>
</template>

<script>
import DiagramChart from './DiagramChart';

export default {
  data() {
    return {
      show: false,
      a: 0,
      b: 0,
      c: 0,
      p1: 0,
      p2: 0,
      q: 0,
      sumY: 0,
      sumX: 0,
      sumMA: 0,
      aY: 0,
    };
  },
  computed: {
    mMax() {
      return (this.q * this.b * this.b / 2) / 8;
    },
    p1Cut() {
      return this.aY * this.a;
    },
    p2Cut() {
      return this.aY * (this.a + this.b) - this.p1 * this.b - this.q * this.b * this.b / 2;
    },
  },
  methods: {
    setData(event) {
      const { name, value } = event.target;

      this[name] = Number(value);
    },
    calculate() {
      this.show = false;
      this.calculateSUMY();
      this.calculateSUMX();
      this.calculateSUMMA();
      this.calculateAy();
      this.show = true;
    },
    calculateSUMY() {
      const sumP = this.p1 + this.p2;
      const qb = this.q * this.b;

      this.sumY = sumP + qb;
    },
    calculateAy() {
      this.aY = this.sumY - this.sumMA;
    },
    calculateSUMX() {
      this.sumX = 0;
    },
    calculateSUMMA() {
      const p1A = this.p1 * this.a;
      const qb = this.q * this.b;
      const b2 = this.b / 2;
      const ab2 = this.a + b2;
      const qbab2 = qb * ab2;
      const ba = this.b + this.a;
      const p2ba = this.p2 * ba;
      const abc = this.a + this.b + this.c;

      const firstPart = p1A + qbab2 + p2ba;

      this.sumMA = firstPart / abc;
    },
  },
  components: {
    DiagramChart,
  },
};
</script>

<style>
.center {
  position: relative;
  width: 100%;
  margin-bottom: 15px;
}

form {
  padding-left: 40px;
  padding-right: 40px;
}

img {
  width: 100%;
}

input {
  width: 50px;
}

.hello {
  overflow-x: hidden;
}
</style>



